* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

button, input[type="submit"] {
  outline: none;
  text-decoration: none;
  text-transform: none;
  font-family: inherit;
  font-size: 100%;
  margin: 0;
  border: none;
}
